import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import GlobalState from './context/GlobalState';
import Polls from './pages/Polls';
import PollPage from './pages/PollPage';
import PollForm from './pages/PollForm';
import Dashboard from './pages/Dashboard';
import MainNavigation from './components/MainNavigation';
import Footer from './components/Footer';
import Login from './pages/Login';
import Game from './pages/Game';
import { PATH } from './pages/Paths';
import { ApolloProvider } from '@apollo/react-hooks';
import { client } from './api/api';

const App = props => {
  return (
    <ApolloProvider client={client}>
      <GlobalState>
        <BrowserRouter>
          <React.Fragment>
            <MainNavigation/>
            <Switch>
              <Route path={PATH.HOME} component={Dashboard} exact />
              <Route path={PATH.POLLS} component={Polls} exact />
              <Route path={PATH.POLLS_SEARCH} component={Polls} />
              <Route path={PATH.POLLFORM} component={PollForm} exact />
              <Route path={PATH.POLL} component={PollPage} />
              <Route path={PATH.LOGIN} component={Login} />
              <Route path={PATH.GAME} component={Game} />
            </Switch>
            <Footer />
          </React.Fragment>
        </BrowserRouter>
      </GlobalState>
    </ApolloProvider>
  );
};

export default App;