import { gql } from 'apollo-boost';

export const GET_POLLS = gql`
  {
    polls {
      id
      title
    }
  }
`;

export const GET_POLL = gql`
  query poll($id: ID!){
    poll(id: $id) {
      id
      title
      description
      type 
      options
      created
      duration
    }
  }
`;

export const GET_POLL_COUNT = gql`
  query getNumPolls {
    getNumPolls
  }
`;


export const CREATE_POLL = gql`
  mutation createPoll(
        $title: String!
        $creator: String
        $description: String!
        $type: PollType!
        $options: String
        $duration: Date!
        $preview: Boolean
      ){
        createPoll(
            title: $title
            creator: $creator
            description: $description
            type: $type
            options: $options
            duration: $duration
            preview: $preview
        ) {
      id
      title
    }
  }
`;