import React from 'react'
import { Icon, Label } from 'semantic-ui-react';
import styled from 'styled-components';
import PropTypes from 'prop-types';


const TagItem = props => {

    const { isTag, close, children } = props;
    const TagLabel = styled(Label)`
        &&& {
            margin: 0 0.5rem 0.5rem ${isTag ? '1rem' : '0rem'};
        }
    `;

  return (
    <TagLabel color='blue' className={isTag ? 'tag ui label' : 'ui label'}>
          {children}
          <Icon onClick={close} name="delete" />
    </TagLabel>
  )
}

TagItem.propTypes = {
  isTag: PropTypes.bool.isRequired,
};

export default TagItem;