import React, { useState } from 'react'
import { Form, Checkbox, } from 'semantic-ui-react';

const YesNoAbstainPoll = props => {
    const { setVoteString, title } = props;
    const [vote, setVote] = useState('');
    const handleChange = e => {
        setVote(e.target.value);
        setVoteString(e.target.value);
        console.log(vote);
    }
    return (
        <React.Fragment>
            <h2 style={{ textAlign: 'center' }}>{title}</h2>
            <Form>
                <Form.Field>
                    <Checkbox
                        radio
                        label='Yes'
                        name='checkboxRadioGroup'
                        id='yes'
                        value='yes'
                        checked={vote === 'yes'}
                        onChange={handleChange}
                    />
                </Form.Field>
                <Form.Field>
                    <Checkbox
                        radio
                        label='No'
                        name='checkboxRadioGroup'
                        id='no'
                        value='no'
                        checked={vote === 'no'}
                        onChange={handleChange}
                    />
                </Form.Field>
            </Form>
        </React.Fragment>
    )
}

export default YesNoAbstainPoll;