import React from 'react';
import LoginForm from '../components/LoginForm';
import logo from '../media/AskDash42.png';
import { Container, Image } from 'semantic-ui-react';

const Login = props => {

  return (
    <React.Fragment>  
      <Container>
      <Image src={logo} centered alt='Ask Dash Logo' />
      <LoginForm></LoginForm>
      </Container>
    </React.Fragment>
  );
};

export default Login;