import React, { useState, useEffect } from 'react'
import { Form } from 'semantic-ui-react'
import TagList from './TagList';
import { useMutation } from '@apollo/react-hooks';
import { CREATE_POLL, GET_POLLS, GET_POLL_COUNT } from '../api/queries';
import { useInputValue, useNumberInputValue } from './hooks';
import { PollTypes } from './PollTypes';

const NewPollForm = props => {
    const ctx = props.ctx;
    const [createPoll] = useMutation(CREATE_POLL);
    const [pollType, setPollType] = useState(ctx.newPoll.type || 'YES_NO_ABSTAIN');
    const [pollDuration, setPollDuration] = useState(ctx.newPoll.duration || 604800000);
    const title = useInputValue(ctx.newPoll.title || '');
    const creator = useInputValue(ctx.newPoll.creator || '');
    const description = useInputValue(ctx.newPoll.description || '');
    const tag = useInputValue('');
    const choice = useInputValue('');
    const rangeFrom = useNumberInputValue('');
    const rangeTo = useNumberInputValue('');
    const [isRange, setIsRange] = useState(false);
    const [tags, setTags] = useState([]);
    const [choices, setChoices] = useState((ctx.newPoll.options) ? ctx.newPoll.options.split(',') : []);
    const [errorMessage, setErrorMessage] = useState('');

    const pollTypes = [
        { key: 'y', text: 'Yes/No/Abstain', value: PollTypes.YES_NO_ABSTAIN },
        { key: 's', text: 'Single Choice', value: PollTypes.SINGLE_CHOICE },
        { key: 'm', text: 'Multiple Choice', value: PollTypes.MULTIPLE_CHOICE },
        { key: 'r', text: 'Ranking', value: PollTypes.MULTIPLE_CHOICE_RANKING },
        { key: 'n', text: 'Number Range', value: PollTypes.NUMBER_RANGE },
    ];
    const durationOptions = [
        { key: '1w', text: '1 Week', value: 604800000 },
        { key: '2w', text: '2 Weeks', value: 1209600000 },
        { key: '4w', text: '4 Weeks', value: 2419200000 },
        { key: '8w', text: '8 Weeks', value: 4838400000 },
        { key: '12w', text: '12 Weeks', value: 7257600000 },
    ];

    const handleSubmit = async (e) => {
        e.preventDefault();
        
        let options = '';
        switch(pollType) {
            case PollTypes.NUMBER_RANGE:
                options = `${rangeFrom.input.value},${rangeTo.input.value},${isRange}`;
                if (rangeFrom.input.value !== '' && rangeTo.input.value !== '' && rangeFrom.input.value >= rangeTo.input.value) {
                    setErrorMessage("'From Range' has to be larger than 'To Range'");
                    return;
                } else {
                    setErrorMessage('');
                }
                break;
            case PollTypes.YES_NO_ABSTAIN:
                options = '';
                break;
            default:
                options = choices.toString();

        }
        
        let variables = {
            title: title.input.value,
            creator: creator.input.value,
            description: description.input.value,
            type: pollType,
            options: options,
            created: Date.now(),
            duration: pollDuration
        };
        try {
            await createPoll(
                {
                    variables: {
                        ...variables,
                        preview: true
                    },
                    refetchQueries: [{ query: GET_POLLS }, { query: GET_POLL_COUNT }]
                });
            ctx.setNewPoll(variables);
            ctx.setNewPollStep(2);
            setErrorMessage('');
        } catch (e) {
            ctx.setNewPoll({});
            setErrorMessage(e.toString());
        }
    }

    const handleCheckbox = e => {
        setIsRange(!isRange);
    }

    const handleKeyUp = e => {
        switch (e.target.id) {
            case 'choicesField':
                if (e.key === 'Enter') {
                    if (choice.input.value.trim() !== '')
                        setChoices([...choices, choice.input.value]);
                    choice.setValue('');
                }
                break;
            case 'tagsField':
                if (e.key === 'Enter') {
                    if (tag.input.value.trim() !== '')
                        setTags([...tags, tag.input.value]);
                    tag.setValue('');
                }
                break;
            default:
                break;
        }

    };

    const returnType = () => {
        let icon = '';
        switch (pollType) {
            case PollTypes.SINGLE_CHOICE:
                icon = 'dot circle outline';
                break;
            case PollTypes.MULTIPLE_CHOICE:
                icon = 'check square outline';
                break;
            case PollTypes.MULTIPLE_CHOICE_RANKING:
                icon = 'sort numeric up';
                break;
            default:
                break;
        }
        switch (pollType) {
            case PollTypes.YES_NO_ABSTAIN:
                return ('');
            case PollTypes.NUMBER_RANGE:
                return (
                    <React.Fragment>
                        <Form.Input
                            id='rangeFromField'
                            icon='step backward'
                            iconPosition='left'
                            placeholder='from'
                            {...rangeFrom.input}
                            onKeyPress={(e) => { e.key === 'Enter' && e.preventDefault(); }}
                        />
                        <Form.Input
                            id='rangeToField'
                            icon='step forward'
                            iconPosition='left'
                            placeholder='to'
                            {...rangeTo.input}
                            onKeyPress={(e) => { e.key === 'Enter' && e.preventDefault(); }}
                        />
                        <Form.Checkbox
                            label='Select Range'
                            defaultChecked={isRange}
                            onClick={handleCheckbox}
                        />
                    </React.Fragment>);
            case PollTypes.SINGLE_CHOICE:
            case PollTypes.MULTIPLE_CHOICE:
            case PollTypes.MULTIPLE_CHOICE_RANKING:
                return (
                    <React.Fragment>
                        <Form.Input
                            id='choicesField'
                            icon={icon}
                            iconPosition='left'
                            placeholder='Enter choices'
                            {...choice.input}
                            onKeyPress={(e) => { e.key === 'Enter' && e.preventDefault(); }}
                            onKeyUp={handleKeyUp}
                        />
                        <TagList tags={choices} setTags={setChoices} display={''} />
                    </React.Fragment>);
            default:
                return (<p>selection unknown</p>);
        }
    }

    useEffect(() => {
    }, [pollType, tags]);



    return (
        <Form>
            <Form.Field>
                <label>Title</label>
                <Form.Input
                    fluid
                    placeholder='Title'
                    {...title.input}
                />
            </Form.Field>
            <Form.Field>
                <label>Creator</label>
                <Form.Input
                    fluid
                    placeholder='Creator'
                    {...creator.input}
                />
            </Form.Field>
            <Form.Field>
                <label>Description</label>
                <Form.TextArea
                    placeholder='Description'
                    {...description.input}
                />
            </Form.Field>
            <Form.Field>
                <label>Type</label>
                <Form.Select
                    options={pollTypes}
                    placeholder='Type'
                    onChange={(_, { value }) => { setPollType(value); }}
                />
            </Form.Field>

            {returnType()}
            <Form.Field>
                <label>Tags</label>
                <Form.Input
                    id='tagsField'
                    icon='tags'
                    iconPosition='left'
                    placeholder='Enter tags'
                    {...tag.input}
                    onKeyPress={(e) => { e.key === 'Enter' && e.preventDefault(); }}
                    onKeyUp={handleKeyUp}
                />
            </Form.Field>

            <TagList tags={tags} setTags={setTags} display={'tags'} />
            <Form.Field>
                <label>Duration</label>
                <Form.Select
                    options={durationOptions}
                    placeholder='Poll Duration'
                    onChange={(_, { value }) => { setPollDuration(value); }}
                />
            </Form.Field>

            <Form.Button content='Next' icon='right arrow' labelPosition='right' onClick={handleSubmit} />
            <p>{errorMessage}</p>
        </Form>
    )
}

export default NewPollForm;


