import React, { useContext, useEffect } from 'react'
import { Button, Divider, Form, Grid, Segment } from 'semantic-ui-react'
import PollsContext from '../context/global-context';
import { useInputValue } from './hooks';

const LoginForm = () => {
    const { setLoggedIn } = useContext(PollsContext);

    const username = useInputValue('');
    const password = useInputValue('');

    useEffect(() => {
      if (localStorage.getItem('mnemotic') !== undefined && localStorage.getItem('mnemotic') !== '') {
        setLoggedIn(true);
      } else {
        setLoggedIn(false);
      }
    }, [setLoggedIn])


    const handleNewWallet = () => {

    }

    const handleLogin = () => {
        setLoggedIn(true);
        localStorage.setItem('mnemotic', `${username.value} ${password.value}`);
    }
    return (
        <Segment placeholder>
          <Grid columns={2} relaxed='very' stackable>
            <Grid.Column>
              <Form>
                <Form.Input
                  id='usernameInput'
                  icon='user'
                  iconPosition='left'
                  label='Username'
                  placeholder='Username'
                  {...username.input}
                />
                <Form.Input
                  id='passwordInput'
                  icon='lock'
                  iconPosition='left'
                  label='Password'
                  type='password'
                  {...password.input}
                />
      
                <Button content='Login' primary onClick={handleLogin} />
              </Form>
            </Grid.Column>
      
            <Grid.Column verticalAlign='middle'>
              <Button content='New Identity' icon='signup' size='big' onClick={handleNewWallet} />
            </Grid.Column>
          </Grid>
      
          <Divider vertical>Or</Divider>
        </Segment>
      );
};

export default LoginForm