import React, { useContext, useEffect, useState } from 'react';
import GlobalContext from '../context/global-context';
import { useQuery } from '@apollo/react-hooks';
import { GET_POLL_COUNT } from '../api/queries';
import { Container, Segment, Header } from 'semantic-ui-react';

const Dashboard = props => {
  const context = useContext(GlobalContext);
  const [numPolls, setNumPolls] = useState('');
  const { loading, error, data } = useQuery(GET_POLL_COUNT);
  const infoSize = {width:175, height:175}

  useEffect(() => {
    if (loading) {
      setNumPolls('loading...');
    } else if (error) {
      setNumPolls('error...');
    } else {
      setNumPolls(data.getNumPolls);
    }
  }, [context, loading, error, data]);

  return (
    <React.Fragment>
      <Container>
        <Header>Dashboard</Header>
        <Segment circular style={infoSize}>
          <Header as='h1'>
            {numPolls}
            <Header.Subheader>Polls live!</Header.Subheader>
          </Header>
        </Segment>
        
      </Container>
    </React.Fragment>
  );
};

export default Dashboard;