import React from 'react';

import { Container, Segment, Icon } from 'semantic-ui-react'

const Footer = props => {
    return (
        <Segment inverted vertical style={{ padding: '2rem 0', position: 'absolute', bottom: '0', right: '0', left: '0', minHeight: '7rem'}}>
            <Container>
                <p style={{ textAlign: 'right'}}>
                    <Icon name="copyright outline"></Icon>Sidem
                </p>
            </Container>
        </Segment>
    );
}
export default Footer;