import React, { useState, useEffect } from 'react';
import { TextArea, Button, Form, Icon } from 'semantic-ui-react';
import copy from 'clipboard-copy';

const VoteGenerator = props => {
    const { voteString, disabled } = props;
    const [copied, setCopied] = useState(false);

    useEffect(() => {
        setCopied(false);
    }, [voteString]);

    const handleCopyVote = () => {
        copy(voteString);
        setCopied(true);
    }

    return (
        <React.Fragment>
            <Form>
                <TextArea rows={2} disabled value={voteString} />
                <Button disabled={disabled} onClick={handleCopyVote}><Icon name="copy" /> {copied ? 'Copied!' : 'Copy Vote String'}</Button>
            </Form>
        </React.Fragment>
    );
}

export default VoteGenerator;