import React, { useState, useContext, useEffect } from 'react';
import { Link } from 'react-router-dom';

import { withRouter } from "react-router";
import { PATH } from '../pages/Paths';
//import Wallet from './Wallet';
import GlobalContext from '../context/global-context';
import logo from '../media/AskDash42.png';
//import { DashClient } from '../api/DashSDK';

const MainNavigation = props => {
  const { pathname } = props.location;
  const ctx = useContext(GlobalContext);
  const { loggedIn } = ctx;
  const [search, setSearch] = useState('');

  async function connect() {
    try {
      //console.log(await DashClient.getDAPIClient().getBestBlockHash())
      console.log('connected');
    } catch (e) {
      console.error('Something went wrong:', e);
    } finally {
      //DashClient.disconnect();
    }
  }

  useEffect(() => {
    if (!loggedIn)
      props.history.push(PATH.LOGIN);
    else
      props.history.push(PATH.HOME);
    connect();
  }, [loggedIn, props.history]);

  const enterSearch = () => {
    ctx.setSearch(search);
    props.history.push(PATH.POLLS_SEARCH+search);
  };

  const handleKeyUp = keyEvent => {
    setSearch(document.getElementById('search-bar').value);
    if(keyEvent.key === 'Enter') {
      enterSearch();
    }
  };

  const handleLogout = () => {
    ctx.setLoggedIn(false);
    localStorage.setItem('mnemotic', '');
  }

  const handleHome = () => {
    props.history.push(PATH.HOME);
  }

  return (
    <div className="ui top large pointing menu" style={{display: ctx.loggedIn ? 'flex' : 'none'}}>
      <img onClick={handleHome} style={{cursor: 'pointer'}} src={logo} className='vertically fitted item' alt='Ask Dash Logo'></img>
      <Link to={PATH.HOME} className={pathname === PATH.HOME ? "item active" : "item"}><i className='icon home'></i>Home</Link>
      <Link to={PATH.POLLS} className={pathname === PATH.POLLS ? "item active" : "item"}><i className='icon list alternate outline'></i>Polls</Link>
      <Link to={PATH.POLLFORM} className={pathname === PATH.POLLFORM ? "item active" : "item"}><i className='icon plus square outline'></i>New Poll</Link>
      <Link to={PATH.GAME} className={pathname === PATH.GAME ? "item active" : "item"}><i className='icon plus square outline'></i>Game</Link>
      <div className="right menu">
        <Link to={'#'} onClick={handleLogout} className={"item"}><i className='icon sign-out'></i>Logout</Link>
        <div className="item">
          <div className="ui transparent icon input">
            <input type="text" id="search-bar" placeholder="Search..." onKeyUp={handleKeyUp}/>
              <i className="search link icon"></i>
              </div>
          </div>
        </div>
      </div>
      );
    }
    export default withRouter(MainNavigation);
