import React, { useState } from 'react'
import { Container, Header, Segment, Label } from 'semantic-ui-react';
import { PollTypes } from './PollTypes';
import { YesNoAbstainPoll, SingleChoicePoll, MultipleChoicePoll, RankingPoll, NumberRangePoll } from './PollModules/index.js'
import VoteGenerator from './VoteGenerator';
import styled from 'styled-components';

const StyledContainer = styled.div`
        padding: 1rem;
    `;

const Poll = props => {
    const { title, creator, description, type, options, created, duration } = props.poll;
    let pollId = '[PollID]';
    if (props.poll.hasOwnProperty('id')) {
        pollId = props.poll.id;
    }
    const [voteString, setVoteString] = useState('');



    const renderPoll = () => {
        switch (type) {
            case PollTypes.YES_NO_ABSTAIN:
                return (<YesNoAbstainPoll 
                    setVoteString={setVoteString} 
                    title="Do you approve?"
                    />);
            case PollTypes.MULTIPLE_CHOICE:
                return (<MultipleChoicePoll 
                    options={options} 
                    setVoteString={setVoteString} 
                    title="Select your approved choices"
                    />);
            case PollTypes.MULTIPLE_CHOICE_RANKING:
                return (<RankingPoll 
                    options={options} 
                    setVoteString={setVoteString} 
                    title="Order by preference"
                    />);
            case PollTypes.NUMBER_RANGE:
                return (<NumberRangePoll 
                    options={options} 
                    setVoteString={setVoteString} 
                    title="Pick your approved Number/Range"
                    />);
            case PollTypes.SINGLE_CHOICE:
                return (<SingleChoicePoll 
                    options={options} 
                    setVoteString={setVoteString} 
                    title="Choose one"
                    />);
            default:
                return ('unknown polltype');
        }
    };

    const renderDescription = () => {
        const desc = description.split('\n');
        let res = [];
        for (let i = 0; i < desc.length; i++) {
            res.push((
                <p key={i}>
                    {desc[i]}
                </p>
            ));
        }
        return res;
    }


    const endDate = new Date(created + parseInt(duration));
    return (
        <Container>
            <Header as='h1' attached='top' block>
                {title}
            </Header>
            <Segment attached>
                <Label color='blue' ribbon>
                    Creator
                </Label>
                <StyledContainer>
                    {creator}
                </StyledContainer>
            </Segment>
            <Segment attached>
                <Label color='blue' ribbon>
                    Description
                </Label>
                <StyledContainer>
                    {renderDescription()}
                </StyledContainer>
            </Segment>
            <Segment attached>
                <Label color='blue' ribbon>
                    Poll
                </Label>
                <StyledContainer>
                    <Segment style={{ margin: '0 5rem', padding: '0.5rem', fontWeight:'bold', fontSize:'1.1rem', lineHeight:'2rem' }}>
                        {renderPoll()}
                    </Segment>
                    <div style={{ marginTop: '1rem' }}>
                        <VoteGenerator voteString={`${pollId}:${voteString}`} disabled={voteString === ''} />
                    </div>
                </StyledContainer>

            </Segment>
            <Segment attached>
                <Label color='blue' ribbon>
                    Deadline
                </Label>
                <StyledContainer>
                    {endDate.toDateString()}
                </StyledContainer>
            </Segment>
        </Container>
    )
}

export default Poll;