import React, { useState, useEffect } from 'react'
import { Form, Radio } from 'semantic-ui-react';

const SingleChoicePoll = props => {
    const { setVoteString, options, title } = props;
    const choices = options.split(',');
    const [vote, setVote] = useState('');

    const handleChange = e => {
        setVote(e.target.value);
        setVoteString(vote.toString());
    }

    useEffect(() => {
        setVoteString(vote.toString());
    }, [vote, setVoteString]);

    const renderOptions = () => {
        let renderedChoices = [];
        for (let key in choices) {
            renderedChoices.push((
                <Form.Field key={`c${key}`}>
                    <Radio
                        label={choices[key]}
                        name='RadioGroup'
                        id={choices[key]}
                        value={choices[key]}
                        checked={vote === choices[key]}
                        onChange={handleChange}
                    />
                </Form.Field>
            ));
        }
        return renderedChoices;
    };


    return (
        <React.Fragment>
            <h2 style={{ textAlign: 'center' }}>{title}</h2>
            <Form>
                {renderOptions()}
            </Form>
        </React.Fragment>
    )
}

export default SingleChoicePoll;