import React from 'react'
import { Step } from 'semantic-ui-react'

const Stepper = props => {
    const { step } = props;
    
    return (
        <Step.Group fluid>
            <Step active={step===1} disabled={step!==1} icon='edit' title='Setup' description='Describe your Poll' />
            <Step active={step===2} disabled={step!==2} icon='info circle' title='Confirm Poll' description='Preview Poll' />
            <Step active={step===3} disabled={step!==3} icon='dollar' title='Billing' description='Pay submission fee' />
        </Step.Group>
    )
}

export default Stepper;