import React from 'react'
import styled from 'styled-components';
import TagItem from './TagItem';
import PropTypes from 'prop-types';

const TagContainer = styled.div`
    margin-bottom: 1rem;
`;

const TagList = props => {

    const {tags, setTags} = props;
    let labels = [];

    const renderLabels = () => {
        tags.map((tag, index) => { 
            labels.push((
                <TagItem
                    isTag={(props.display === 'tags')}
                    key={index} 
                    close={() => {
                        let temp = [...tags];
                        temp.splice(index, 1);
                        setTags(temp);
                    }}
                >
                    {tag}
                </TagItem>
            )); return true;
        });
    };

    renderLabels();

    return  (
        <TagContainer>
            {labels}
        </TagContainer>
    );
}
TagList.propTypes = {
    setTags: PropTypes.func.isRequired,
    display: PropTypes.string.isRequired,
};

export default TagList;