import React, { useState, useEffect } from 'react'
import { List } from 'semantic-ui-react';
import {SortableContainer, SortableElement} from 'react-sortable-hoc';
import arrayMove from 'array-move';

const SortableItem = SortableElement(({value}) => <List.Item style={{cursor:'grab'}}>{value}</List.Item>);

const SortableList = SortableContainer(({items}) => {
    return (
      <List celled ordered>
        {items.map((value, index) => (
          <SortableItem key={`c${index}`} index={index} value={value} />
        ))}
      </List>
    );
  });

const RankingPoll = props => {
    const { setVoteString, options, title } = props;
    const [choices, setChoices] = useState(options.split(','));
    const [vote, setVote] = useState([]);

    const onSortEnd = ({oldIndex, newIndex}) => { 
        setChoices(arrayMove(choices, oldIndex, newIndex));
        setVote(choices);
        setVoteString(vote.toString());
    };

    useEffect(() => {
        setVoteString(vote);
    }, [vote, setVoteString]);

    return (
        <React.Fragment>
            <h2 style={{ textAlign:'center'}}>{title}</h2>
            <SortableList lockAxis='y' items={choices} onSortEnd={onSortEnd} />
        </React.Fragment>
    )
}


export default RankingPoll;