import React from 'react'
import { Segment, Button, Header } from 'semantic-ui-react';
import { CREATE_POLL, GET_POLLS, GET_POLL_COUNT } from '../api/queries';
import { useMutation } from '@apollo/react-hooks';
import QRCode from 'qrcode.react';

const PaymentRequest = props => {
    const ctx = props.ctx;
    const [createPoll] = useMutation(CREATE_POLL);

    const handleBack = () => {
        ctx.setNewPollStep(2);
    }

    const handleConfirm = async () => {
        try {
            await createPoll(
                {
                    variables: {
                        ...ctx.newPoll,
                        preview: false
                    },
                    refetchQueries: [{ query: GET_POLLS }, { query: GET_POLL_COUNT }]
                });
        } catch (e) {
            ctx.setNewPoll({});
        }

    }

    return (
        <React.Fragment>
            <Header as='h1' attached='top' block>
                Payment Request
            </Header>
            <Segment attached style={{ textAlign: 'center' }}>
                <QRCode level="H" size="256" value="hey" />
            </Segment>
            <Button content='Cancel' icon='left arrow' labelPosition='left' onClick={handleBack} />
            <Button content='Pay' icon='right arrow' labelPosition='right' onClick={handleConfirm} />
        </React.Fragment>
    )
}

export default PaymentRequest;