import React, { useState, useEffect} from 'react'
import { Form } from 'semantic-ui-react';
import { Slider } from "react-semantic-ui-range";
import { useNumberInputValue } from '../hooks';



const NumberRangePoll = props => {
    const { setVoteString, options } = props;
    const optionArray = options.split(',');
    const minValue = parseInt(optionArray[0]);
    const maxValue = parseInt(optionArray[1]);
    const rangeFrom = useNumberInputValue('', minValue, maxValue);
    const rangeTo = useNumberInputValue('', minValue, maxValue);
    let initialValue = [];
    if (optionArray[2] === 'true') {
        initialValue = [minValue, maxValue];
    } else {
        initialValue = [parseInt((minValue + maxValue) / 2)]
    }
    const [vote, setVote] = useState('');

    useEffect(() => {
        setVoteString(vote.toString());
    }, [vote, setVoteString]);

    const settings = {
        start: initialValue,
        min: minValue,
        max: maxValue,
        step: parseInt((maxValue - minValue) / 100),
        onChange: value => {
            if (optionArray[2] === 'true') {
                rangeFrom.setValue(value[0]);
                rangeTo.setValue(value[1]);
            } else {
                rangeFrom.setValue(value);
            }
            setVote(value);
        }
    };

    const renderFields = () => {
        if (optionArray[2] === 'true') {
            return (
                <React.Fragment>
                    <h2 style={{ textAlign: 'center' }}>Pick your approved Range</h2>
                    <Slider multiple color="blue" settings={settings} style={{ height: '5rem', padding: '1rem 3rem 0' }} />
                    <Form.Input
                        id='rangeFromField'
                        icon='step backward'
                        iconPosition='left'
                        placeholder="From"
                        {...rangeFrom.input}
                        onKeyPress={(e) => { e.key === 'Enter' && e.preventDefault(); }}
                    />
                    <Form.Input
                        id='rangeToField'
                        icon='step forward'
                        iconPosition='left'
                        placeholder="To"
                        {...rangeTo.input}
                        onKeyPress={(e) => { e.key === 'Enter' && e.preventDefault(); }}
                    />

                </React.Fragment>
            );
        } else {
            return (
                <React.Fragment>
                    <h2 style={{ textAlign: 'center' }}>Pick your approved Number</h2>
                    <Slider color="blue" settings={settings} style={{ height: '5rem', padding: '1rem 3rem 0' }} />
                    <Form.Input
                        id='valueField'
                        placeholder="Number"
                        {...rangeFrom.input}
                        onKeyPress={(e) => { e.key === 'Enter' && e.preventDefault(); }}
                    />
                </React.Fragment>
            );
        }
    }

    return (
        <React.Fragment>
            {renderFields()}
        </React.Fragment>
    )
}

export default NumberRangePoll;