import React, { useState, useEffect } from 'react'
import { Form, Checkbox } from 'semantic-ui-react';

const MultipleChoicePoll = props => {
    const { setVoteString, options, title } = props;
    const choices = options.split(',');
    const [vote, setVote] = useState([]);

    const handleChange = e => {
        if (e.target.checked) {
            setVote([...vote, e.target.value]);
        } else {
            let temp = [...vote];
            temp.splice(vote.indexOf(e.target.value), 1);
            setVote(temp);
        }
        setVoteString(vote.toString());
    }

    useEffect(() => {
        setVoteString(vote.toString());
    }, [vote, setVoteString]);

    const renderOptions = () => {
        let renderedChoices = [];
        for (let key in choices) {
            renderedChoices.push((
                <Form.Field key={`c${key}`}>
                    <Checkbox
                        label={choices[key]}
                        name='checkboxGroup'
                        id={choices[key]}
                        value={choices[key]}
                        checked={vote.includes(choices[key])}
                        onChange={handleChange}
                    />
                </Form.Field>
            ));
        }
        return renderedChoices;
    };


    return (
        <Form>
            <h2 style={{ textAlign: 'center' }}>{title}</h2>
            {renderOptions()}
        </Form>
    )
}

export default MultipleChoicePoll;