import { useState } from 'react';

export const useInputValue = initialValue => {
    const [value, setValue] = useState(initialValue);
    return { 
        input:{
            value, 
            onChange: e => setValue(e.target.value)
        },
        setValue
    };
};

export const useNumberInputValue = (initialValue, min=0, max=9999999) => {
    const [value, setValue] = useState(initialValue);
    return { 
        input:{
            value, 
            onChange: e => {
                const re = /^[0-9\b]+$/;
                let val = e.target.value;
                if (val === '' || re.test(val)) {
                    val = parseInt(val);
                    if(val <= max && val >= min) {
                        setValue(val);
                    }
                }
            }
        },
        setValue
    };
};